const initialState = {
    countries: [],
    allCountries: [],
    details: [],
    resultPost: [],
    activities: [],
}



function rootReducer(state = initialState, action) {
    switch (action.type) {
        case "GET_COUNTRIES":

            return {
                ...state,
                countries: action.result,

            };





        default:
            return { ...state };



    }
}






export default rootReducer;
